html,
body {
  height: 100%;
  font-size: 14px;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

a {
  color: #44a340;
  font-size: 14px;
}

a:hover {
  color: #fff;
  background: #44a340;
  text-decoration: none;
}

#nprogress .bar {
  background: #44a340;
}
#nprogress .spinner-icon {
  border-top-color: #44a340;
  border-left-color: #44a340;
}
